/**
 * 
 *  public Long id; 
    public String text;
    public TodoStatus status = TodoStatus.IMCOMPLETED;
 */
export type TodoEntity = {
    id: number;
    text: string;
    status: TodoStatus;
    deadline: Date;
    registrantId: String;         // 
    assignedUserId: String;       // 
};

export enum TodoStatus {
    COMPLETED = "COMPLETED",
    IMCOMPLETED = "IMCOMPLETED",
    DELETED = "DELETED"
}