//    use   
import axios from "axios";
import { useState } from "react";
import { Constants } from "../utils/Constants";
import { TodoEntity } from "./api/TodoEntity";

//      
export const useTodos = () => {
    const [todoEntities, setTodoEntities] = useState<Array<TodoEntity>>([]);
    
    // curl -X POST http://localhost:8080/putTodo -H "Content-Type: application/json" -d "{ \"text\": \"johnny\" }"
    const addTodo = async (text :string, deadline :Date) => {
      await axios.post(`${Constants.ENV}/putTodo`, {
          text: text,
          deadline: deadline,
          assignedUserId: "self",
          registrantId: "self"
      })
      //   
      .then((response) => {
        console.log(response);
        getAllTodos();
      })
      //   
      .catch((error)=>{
        console.log(error);
      })
    }

    const getAllTodos = async () => {
      console.log("GetAllTodos")
        await axios({
          url: `${Constants.ENV}/getTodo`,
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          }
        })
        .then((response) => {
            console.log(`getAllTodos : ${response}`);
            const res :Array<TodoEntity> = response.data.map((todo :TodoEntity) => ({
              id: todo.id,
              text: todo.text,
              status: todo.status,
              deadline: todo.deadline,
              assignedUserId: todo.assignedUserId,
              registrantId: todo.registrantId
            }));
            setTodoEntities(res);
        })
        .catch((error)=>{
            console.log(error);
        })
    }

    // @GetMapping("/deleteTodo/{id}")
    const deleteTodo = async (id:number) => {
        await axios({
          url: `${Constants.ENV}/deleteTodo/${id}`,
          method: 'GET'
        })
        .then((response) => {
          console.log(response);
        })
        .catch((error)=>{
          console.log(error);
        })
    }

    const completeTodo = async (id:number) => {
      await axios({
        url: `${Constants.ENV}/completeTodo/${id}`,
        method: 'GET'
      })
      .then((response) => {
        console.log(response);
      })
      .catch((error)=>{
        console.log(error);
      })
  }

    //      
    return {
      getAllTodos, addTodo, deleteTodo, completeTodo, todoEntities, setTodoEntities
    };
};